.logo {
    display: flex;
    width: 20rem;
    justify-content: center;
    align-items: center;

    &__img {
        background-image: linear-gradient(to right bottom, $color-primary, $color-secondary);
        width: 5rem;
        height: 5rem;
        border-radius: 50%;
        position: relative;
        margin-right: 1.5rem;
    }

    &__icon {
        position: absolute;
        color: $color-white;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-size: 2.6rem;
    }

    &__text {
        font-size: 2.5rem;
        font-weight: 600;
        color: $color-black;
    }
}
